.fip .fip-editor-container .fip-output {
	display: none;
	border: 1px solid #e0e1e3;
	background-color: rgb(250.7966101695, 250.9322033898, 251.2033898305);
	margin-bottom: 10px;
	padding: 15px;
}

.fip .fip-editor-container .fip-output-active {
	display: block;
}

.fip .fip-editor-container .fip-output-active-success {
	border-left: 4px solid #39b54a;
}

.fip .fip-editor-container .fip-output-active-error {
	border-left: 4px solid #93003c;
}

.fip .fip-editor-container .fip-thumb-preview img {
	margin-bottom: 10px;
}

.fip .fip-editor-container .fip-custom-select {
	overflow: hidden;
	padding: 3px 12px;
	position: relative;
	width: 100%;
}

